.section-about {
  background-color: $color-grey-light-1;
  padding: 20rem 0;
  margin-top: -20vh;
  padding-bottom: 1rem;

  @include respond(tab-port) {
    padding: 20rem 0;
    padding-bottom: 1rem;
  }
}

.section-projects {
  padding-top: 2rem;
  background-color: $color-grey-light-1;
}

